import React, {useState, useEffect} from 'react'
import "../App.css";
import logoH from "../recursos/logo-horizontal.png"
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import {Col, Button, Form, FormGroup, Label, Input, FormText } from 'reactstrap';
import {db,storage} from "../database/firebase.js"
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import gama from "../recursos/gaman5.png"




export default function AgregarCategoria() {
    const[state, setState] = useState([])
    const[imagenes, setImagenes] = useState([]);
  const[Id, setId] = useState([]);
  useEffect(() => {
    db.collection("productos").onSnapshot((querySnapshot)=>{
        const arreglo = [];
        const ids = [];
        querySnapshot.forEach((doc) => {
            let id = doc.id;
            let nombre = doc.data();
            arreglo.push(nombre);
            ids.push(id);
            
        })
        setImagenes(arreglo);
        setId(ids);
    });
 
   
  },[]);
    const acordion = () => {
        let variable = document.getElementById("div1");
      if( variable.style.display === "block"){
        variable.style.display = "none";
      }else{
        variable.style.display = "block";
      }
        
        
    }
  
    const cerrarMenu = () => {
        let variableC = document.getElementById("opciones");
        let x = document.getElementById("x");
        if( variableC.style.display === "block"){
          variableC.style.display = "none";
          x.className = "fa fa-bars"
        }else{
          variableC.style.display = "block";
          x.className = "fa fa-close"
        }
          
    }
    const handle = (e) => {
     const{name, value} = e.target;
     setState({...state,[name]: value})
    }

   
    const notify = () =>
    toast(
      
      <div id="cartext">
      <p>Categoria Agregada</p>
      <img id="i" src={gama} alt="imagen"/>
    </div>
    
    );
      
    let cargar =  async(e) => {
        let foto= e.target.files[0];
        let nombre= e.target.files[0].name; 
       
        if(foto)
        {
   
            await  storage
            .ref()
            .child(`images/${nombre}`).put(foto)
            .then(resolve => {
                console.log(resolve);
            })
            .catch(error => {
                console.log(error);
            });

            await storage
            .ref(`images/${nombre}`)
            .getDownloadURL()
            .then(resolve => {
               db.collection("productos").add({
                  producto: state.producto,
                  foto: resolve
                 })
                 
                 notify()
            })
            .catch(error =>{
              console.log(error);
            });
         
            
          
      }else{
          console.log("error");
      } 
   }
  const p = 1;
    return (
        <div className="contenedorPanel">
   
        <div className="vista">
         <div className="opcionesTop">
           <div className="itemsO">
               <p>INVENTARIO</p>
               <Link to="/agregarCategoria">
               <p>Agregar Categoria</p>
               </Link>
           </div>
         </div>
              <h1>AGREGAR CATEGORIA</h1>
         
      
  
  <Form  className="agregarCategoria">
    
        
        <div className="grupoC">
           <label className="label">Categoria</label>
            <input type="text" onChange={handle} name="producto" placeholder="Producto"/>
        </div>
        <div className="grupoC">
        <label className="label">Imagen</label>
        <input onChange={cargar} type="file" />
        </div>
        <div>
         
        <input type="submit" className="enviar" value="volver"/>
       
        </div>
      </Form>
      
        </div>
        
        <div onClick={cerrarMenu} id="closes">
              <i id="x" className="fa fa-bars "></i>
        </div>
        <div id="opciones">
              <ol>
                  <div className="logoHorizontal">
                  <img src={logoH} alt="imagen"/>
                  
                  </div>
                  
                  <h2 onClick={acordion}>INVENTARIO</h2>
                 
                  <div id="div1">
                  <ul>
                  {imagenes.map((t)=> {
                     return(
                      <a href={"/dbItems/"+t.producto+":1"}>
                      <li>{t.producto}</li>
                      </a>
                      
                     );
                  })}
                
            
                  </ul>
                  </div>
                  <a href={"/ventas/"+p}>
                  <h2>VENTAS</h2>
                  </a>
                  <a href={"/facturasVentas/"+p}>
                  <h2>FACTURAS VENTAS</h2>
                  </a>
                  <a href={"/facturasInsumos/"+p}>
                  <h2>FACTURAS INSUMOS</h2>
                  </a>
                  <a href={"/proveedores/"+p}>
                  <h2>LISTA PROVEEDORES</h2>
                  </a>
                  <a href={"/dbUsuarios/"+p}>
                  <h2>USUARIOS PLATAFORMA</h2>
                  </a>
                  <a href={"/dbNewsletter/"+p}>
                  <h2>NEWSLETTER</h2>
                  </a>
                 
                  <Link to="/filtro">
                  <h2>FILTRO</h2>
                  </Link>
              </ol>
            </div>
            
        <ToastContainer
      position="bottom-center"
      autoClose={false}
      newestOnTop={false}
      closeOnClick
      rtl={false}
      pauseOnFocusLoss
      draggable
    />
    </div>
    )
}
