import React,{useEffect, useState} from 'react'
import "../App.css";
import logoH from "../recursos/logo-horizontal.png"
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import {Col, Button, Form, FormGroup, Label, Input, FormText } from 'reactstrap';
import {db,storage} from "../database/firebase.js"
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import gama from "../recursos/gaman5.png"


export default function DbItems(props) {
  const[imagenes, setImagenes] = useState([]);
  const[Ind, setInd] = useState([]);
  const[messag, setMessag] = useState([]);
  
  const[arrayId, setArrayId] = useState([]);
  const[state, setState] = useState([]);
  const[productos, setProductos] = useState([]);
  useEffect(() => {
    
    db.collection("productos").onSnapshot((querySnapshot)=>{
      const arreglo = [];
      const ids = [];
      querySnapshot.forEach((doc) => {
          let id = doc.id;
          let nombre = doc.data();
          arreglo.push(nombre);
          ids.push(id);
          
      })
      setProductos(arreglo);
    })
      let vaa = props.match.params.id.toString();
    let nombreD = vaa.substring(0, vaa.lastIndexOf(":"))
      db.collection(nombreD).onSnapshot((querySnapshot)=>{
        const indices = [];
        querySnapshot.forEach((doc) => {
            let id = doc.id;
            let nombre = doc.data();
            indices.push(nombre); 
        })
        setInd(indices);
     
  })

    let p = vaa.substring(vaa.lastIndexOf(":")+1, vaa.length)
    let a = p ? Number(p) : 1;
    let post = 2;
    let inicio = (a > 1) ? (a * post - post)+1 : 1;
  
    db.collection(nombreD)
    .orderBy("indice")
    .startAt(inicio)
    .limit(post)
    .onSnapshot((query) => {
      const arr = [];
      query.forEach((doc) => {
        const {
          indice,
          referencia,
          producto,
          descripcion,
          precioA,
          precio,
          colores,
          foto
         
        } = doc.data();

        arr.push({
          id: doc.id,
          indice,
          referencia,
          producto,
          descripcion,
          precioA,
          precio,
          colores,
          foto
        });
       
      });
      setState(arr);
  
    });
 
   
  },[]);

  const refrescar = () => {
    window.location.reload()
  }
  const handle = (e) => {
    const{name, value} = e.target;
    setImagenes({...imagenes,[name]: value})
   }
    const acordion = () => {
        let variable = document.getElementById("div1");
      if( variable.style.display === "block"){
        variable.style.display = "none";
      }else{
        variable.style.display = "block";
      }
      
        
    }
    let vaa = props.match.params.id.toString();
    let nombreD = vaa.substring(0, vaa.lastIndexOf(":"))
    let p = vaa.substring(vaa.lastIndexOf(":")+1, vaa.length)
    const notify = () =>
    toast(
      
      <div id="cartext">
      <p>Producto Agregado</p>
      <img id="i" src={gama} alt="imagen"/>
    </div>
    
    );
    const notify2 = () =>
    toast(
      
      <div id="cartext">
      <p>Registro eliminado</p>
      <img id="i" src={gama} alt="imagen"/>
    </div>
    
    );
   
    let cargar =  async(e) => {
      const pictureNames = [];
      for (var i = 0; i < e.target.files.length; i++) {
        var imageFile = e.target.files[i];
        var imageName = e.target.files[i].name;
 
      let foto= e.target.files[0];
      
 
      if(imageFile)
      {
 
          await  storage
          .ref()
          .child(`images/${imageName}`).put(imageFile)
          .then(resolve => {
              console.log(resolve);
          })
          .catch(error => {
              console.log(error);
          });
          pictureNames.push(imageName);
       
    }else{
        console.log("error");
    } 
  }
  var nombre = e.target.files[0].name;
  storage
  .ref(`images/${nombre}`)
  .getDownloadURL()
  .then(resolve => {
    db.collection(nombreD).add({
      indice: (Number(state.length)+1),
       referencia: imagenes.referencia,
       producto: imagenes.producto,
       descripcion: imagenes.descripcion,
       cantidad: imagenes.cantidad,
       precioA: imagenes.precioA,
       precio: imagenes.precio,
       color1: imagenes.color1 ? imagenes.color1 : "",
       color2:imagenes.color2 ? imagenes.color2 : "",
       color3: imagenes.color3 ? imagenes.color3 : "",
       color4: imagenes.color4 ? imagenes.color4 : "",
       color5: imagenes.color5 ? imagenes.color5 : "",
       foto: resolve
      })

  })

  

    const o = [];
    db
    .collection(nombreD)
    .where("producto", "==", imagenes.producto)
    .onSnapshot((query) => {
      query.forEach((doc) => {
    
       pictureNames.map((r)=>{
        storage
        .ref(`images/${r}`)
        .getDownloadURL()
        .then(resolve => {
          db.collection("imagenes").add({
            idR : doc.id,
            foto: resolve
          });
     
        })
        .catch(error =>{
          console.log(error);
        });
        
       
      })
          setMessag("Cargando");
          notify();
          setMessag("");
          console.log("done")
      });
      
      
    });
  
  

  

   
 }
    const cerrarMenu = () => {
        let variableC = document.getElementById("opciones");
        let x = document.getElementById("x");
        if( variableC.style.display === "block"){
          variableC.style.display = "none";
          x.className = "fa fa-bars"
        }else{
          variableC.style.display = "block";
          x.className = "fa fa-close"
        }
          
    }
    const deleteUserId = (id) => {
      console.log(id)
     
        db.collection(nombreD).doc(id).delete();
        db.collection("imagenes").where("idR", "==", id).onSnapshot((querySnapshot) => {
      
          querySnapshot.forEach((doc) => {
            db.collection("imagenes").doc(doc.id).delete();
        });
      });
     
        
      
      notify2()
    };
    
  

  
 
  

    return (
        <div className="contenedorPanel">
   
        <div className="vista">
         <div className="opcionesTop">
           <div className="itemsO">
               <p>INVENTARIO</p>
               <Link to="/agregarCategoria">
               <p>Agregar Categoria</p>
               </Link>
           </div>
         </div>
            
              <h1>{nombreD}</h1>
              <div className="buscadorD">
        <input type="search" />
        <i className="fa fa-search iconoc"></i>
      </div>
      <div className="tablaPeluches">
      
  <table>
    <tr>
      
      <th>Id</th>
      <th>Referencia</th>
      <th>Producto</th>
      <th>Decripción</th>
      <th>Cantidad</th>
      <th>Precio(Antes)</th>
      <th>Precio(Actual)</th>
      <th>Colores</th>
      <th>Imagen Principal</th>
  
      <th>-
      </th>
      
    </tr>
    {state.map((k)=>{
      return(
    
    <tr onDoubleClick={()=>deleteUserId(k.id)}>
     
      <td>{k.id}</td>
      <td>{k.referencia}</td>
      <td>{k.producto}</td>
      <td>{k.descripcion}</td>
      <td>{k.cantidad}</td>
      <td>{k.precioA}</td>
      <td>{k.precio}</td>
      <td>{k.colores}</td>
      <td><img src={k.foto} width={50}/></td>
      <td><Link to={"/editar/"+nombreD+":"+k.id}><p className="edit">Editar</p></Link></td>
     
    </tr>
    
    
   );
  })}
  </table>
  
  
  </div>
  <div className="paginacion">
    {(Number(p) == 1)?(
       <p className="disabled"><i  className="fa fa-chevron-left"></i></p>
    ):<a href={"/dbItems/"+nombreD+":"+(Number(p)-1)}><i  className="fa fa-chevron-left "></i></a>}
    
       <p>{p}</p>
      
       {(Number(p) == Ind.length)?(
       <p className="disabled"><i  className="fa fa-chevron-right"></i></p>
    ):<a href={"/dbItems/"+nombreD+":"+(Number(p)+1)}><i  className="fa fa-chevron-right "></i></a>}
  </div>
  <Form>
      <h2 className="titleh2">NUEVO PRODUCTO</h2>
      
      <div className="grupo">
            <input type="text" onChange={handle} name="referencia" placeholder="Referencia"/>
        </div>
        <div className="grupo">
            <input type="text" onChange={handle} name="producto" placeholder="Producto"/>
        </div>
        <div className="grupo">
        <textarea className="textArea"  onChange={handle}   name="descripcion" placeholder="Descripcion"/>
        </div>
        <div className="grupo">
            <input type="text"  onChange={handle}  name="cantidad" placeholder="Cantidad"/>
        </div>
        <div className="grupo">
            <input type="text"  onChange={handle}  name="precioA" placeholder="Precio Anterior"/>
        </div>
        <div className="grupo">
            <input type="text"  onChange={handle}  name="precio" placeholder="Precio Actual(Oferta)"/>
        </div>
        <div className="grupo">
            <input type="text"  onChange={handle}  name="color1" placeholder="Color1"/>
        </div>
        <div className="grupo">
            <input type="text"  onChange={handle}  name="color2" placeholder="Color2"/>
        </div>
        <div className="grupo">
            <input type="text"  onChange={handle}  name="color3" placeholder="Color3"/>
        </div>
        <div className="grupo">
            <input type="text"  onChange={handle}  name="color4" placeholder="Color4"/>
        </div>
        <div className="grupo">
            <input type="text"  onChange={handle}  name="color5" placeholder="Color5"/>
        </div>
        <div className="grupoC">
        <p>{messag}</p>
        <label className="label">Imagen</label>
        <input onChange={cargar} type="file" name="foto[]" multiple/>
        </div>
        <div>
         
        </div>
      </Form>
      
        </div>
        
        <div onClick={cerrarMenu} id="closes">
              <i id="x" className="fa fa-bars "></i>
        </div>
        <div id="opciones">
          <ol>
              <div className="logoHorizontal">
              <Link to="/panel">
              <img src={logoH}/>
              </Link>
              </div>
              
              <h2 onClick={acordion}>INVENTARIO</h2>
             
              <div id="div1">
                  <ul>
                  {productos.map((t)=> {
                     return(
                      <a href={"/dbItems/"+t.producto+":1"}>
                      <li>{t.producto}</li>
                      </a>
                      
                     );
                  })}
                
            
                  </ul>
                  </div>
                  <a href={"/ventas/"+p}>
                  <h2>VENTAS</h2>
                  </a>
                  <a href={"/facturasVentas/"+p}>
                  <h2>FACTURAS VENTAS</h2>
                  </a>
                  <a href={"/facturasInsumos/"+p}>
                  <h2>FACTURAS INSUMOS</h2>
                  </a>
                  <a href={"/proveedores/"+p}>
                  <h2>LISTA PROVEEDORES</h2>
                  </a>
                  <a href={"/dbUsuarios/"+p}>
                  <h2>USUARIOS PLATAFORMA</h2>
                  </a>
                  <a href={"/dbNewsletter/"+p}>
                  <h2>NEWSLETTER</h2>
                  </a>
                  <Link to="/filtro">
                  <h2>FILTRO</h2>
                  </Link>
          </ol>
         
        </div>
        <ToastContainer
      position="bottom-center"
      autoClose={false}
      newestOnTop={false}
      closeOnClick
      rtl={false}
      pauseOnFocusLoss
      draggable
    />
        
    </div>
    )
}
